import React from 'react';
import SearchBar from './Searchbar';
import _ from "lodash";







class App extends React.Component {
    state = {
        videos: [],
        selectedVideo: null
    }
   
    render() {
        return (
            <div className='ui container' style={{marginTop: '1em'}}>
                <SearchBar handleFormSubmit={this.handleSubmit}/>
                {_.times(100, (i) => (
                    <div key={i}>
                
                      
                        <video id='' width="auto" height="auto" controls>
  <source src={require('./assets/video_example.mp4')} type="video/mp4"/>
     </video>
         
     <div>
                            
                            <div>         <title>Rotating planet</title>
                            </div>
                      <input type='text' id="meta-description" name="description" value="Vido de la plante en train de tourner." />
                              <input type='text' id=	"video" value="/video_example.mp4"/>
                              <input type='text' id=	"tags" value="planet,earth,science,space"/>
                              <input type='text' id=	"duration" value="00.31"/>
                              <input type='text' id=	" Creater" value="planatoryGuy"/>
                              <input type='text' id=	"views" value="19876"/>
                              <input type='text' id=	"created_at" value="2021-05-01T08:00:00.00Z"/>
                              <input type='text' id=	"like" value="10"/>
                              <input type='text' id=	"dislike" value="2"/>
                          
                            <div class="content"> Some Content </div>
                          </div>
                          <div class="container mt-5">
    <div class="d-flex justify-content-center row">
        <div class="col-md-8">
            <div class="d-flex flex-column comment-section">
                <div class="bg-white p-2">
                   
                    <div class="mt-2">
                        <p class="comment-text">woowwww</p>
                    </div>
                </div>
                <div class="bg-white">
                    <div class="d-flex flex-row fs-12">
                        <div class="like p-2 cursor"><i class="fa fa-thumbs-o-up"></i><span class="ml-1">Like</span></div>
                        <div class="like p-2 cursor"><i class="fa fa-commenting-o"></i><span class="ml-1">Comment</span></div>
                        <div class="like p-2 cursor"><i class="fa fa-share"></i><span class="ml-1">Share</span></div>
                    </div>
                </div>
                <div class="bg-light p-2">
                    <div class="mt-2 text-right"><button class="btn btn-primary btn-sm shadow-none" type="button">Post comment</button><button class="btn btn-outline-primary btn-sm ml-1 shadow-none" type="button">Cancel</button></div>
                </div>
            </div>
        </div>
    </div>
</div>
                      
                  
                                       
     </div> ))}

                  
                        </div>
             

        
        )
    }
}

export default App;


